export default {
  data: function data() {
    return {
      loading: false,
      pageSize: [10, 20, 50, 100],
      page: this.$route.query.page ? Number(this.$route.query.page) : 1,
      rowsPerPage: localStorage.getItem("c_allOrdersRowsPerPage") ? Number(localStorage.getItem("c_allOrdersRowsPerPage")) : 20,
      totalCount: 0,
      totalPage: 0,
      tableData: [],
      payStatusDict: {
        1: {
          name: "Pending",
          style: "tx-warning"
        },
        2: {
          name: "Paid",
          style: "tx-success"
        },
        3: {
          name: "Refund",
          style: "tx-danger"
        },
        4: {
          name: "Cancelled",
          style: "tx-info"
        }
      },
      payTypeDict: {
        1: {
          name: "VIP Membership"
        },
        2: {
          name: "Recharge"
        },
        3: {
          name: "Used"
        }
      },
      payPlatformDict: {
        1: {
          name: "PayPal"
        },
        2: {
          name: "Checkout"
        }
      }
    };
  },
  mounted: function mounted() {
    this.getItem();
  },
  watch: {
    $route: "gotoPage"
  },
  methods: {
    getItem: function getItem() {
      var _this = this;

      this.loading = true;
      var params = {
        page: this.page,
        rowsPerPage: this.rowsPerPage
      };
      this.loading = false;
      this.$apiCall("api.PayRecord.findByUser", params, function (r) {
        if (r.ErrorCode == "9999") {
          _this.tableData = r.Data.Results;
          _this.totalPage = r.Data.Pagination.totalPage;
          _this.totalCount = r.Data.Pagination.totalCount;
        } else {
          _this.$elementMessage(r.Message, "error");
        }
      });
    },
    toPage: function toPage(val) {
      if (val != this.$route.query.page) this.$router.push({
        query: {
          page: val
        }
      });
    },
    changePageSize: function changePageSize(val) {
      this.rowsPerPage = val;
      localStorage.setItem("c_payListRowsPerPage", val);
      this.getItem();
    },
    gotoPage: function gotoPage() {
      this.page = this.$route.query.page ? this.$route.query.page : 1;
      this.getItem();
    }
  }
};